"use client";

import React, { useState, useEffect, useCallback } from "react";
import { ProjectCard } from "./ProjectCard";
import { motion, AnimatePresence } from "motion/react";
import { ChevronLeft, ChevronRight } from "lucide-react";

const projects = [
  {
    name: "A2-GO!",
    description:
      "An AR-based mobile game inspired by Pokémon GO, enabling users to plant and grow virtual trees at real-world landmarks.",
    technologies: ["Unity", "C#", "JIRA"],
    links: [{ label: "Video", url: "https://www.youtube.com/watch?v=example" }],
  },
  {
    name: "ritij.tech",
    description:
      "A platform for scraping and managing internship and co-op listings for the 2024-2025 school year.",
    technologies: ["Next.js", "Supabase", "BeautifulSoup"],
    links: [{ label: "Website", url: "https://www.ritij.tech" }],
  },
  {
    name: "Barterville",
    description:
      "A game where users negotiate trades with AI characters, starting with lower-value items and aiming for higher-value trades.",
    technologies: ["Next.js", "Python"],
    links: [{ label: "Website", url: "https://ai.google.dev" }],
  },
  {
    name: "Image Background Swapper",
    description:
      "A website allowing users to upload pictures and replace the background with an auto-generated background based on text queries.",
    technologies: ["PyTorch", "Flask", "Python", "Next.js"],
    links: [
      {
        label: "GitHub",
        url: "https://github.com/ritij/image-background-swapper",
      },
    ],
  },
  {
    name: "iMessage Wrapped",
    description:
      "A utility application offering insights and analytics about your iMessage usage over the past year.",
    technologies: ["PyTorch", "Flask", "Python", "Next.js"],
    links: [
      { label: "GitHub", url: "https://github.com/ritij/imessage-wrapped" },
    ],
  },
  {
    name: "Disease Predictor",
    description:
      "A disease prediction model employing machine learning to analyze data and predict diseases early.",
    technologies: ["Flask", "Pandas", "Next.js", "Numpy"],
    links: [
      { label: "GitHub", url: "https://github.com/ritij/disease-predictor" },
    ],
  },
  {
    name: "WordPress iOS - Open Source Contributions",
    description:
      "Contributed to WordPress iOS by addressing issues related to post-navigation statistics caching and unresponsive URLs.",
    technologies: ["Git", "Swift"],
    links: [
      {
        label: "GitHub",
        url: "https://github.com/ritij/wordpress-ios-contributions",
      },
    ],
  },
];

const Projects = () => {
  const [currentIndex, setCurrentIndex] = useState(0);
  const [direction, setDirection] = useState(0);
  const [isAutoplay, setIsAutoplay] = useState(true);
  const [touchStart, setTouchStart] = useState(0);
  
  // Calculate how many projects to show based on screen size
  const [itemsToShow, setItemsToShow] = useState(1);
  
  useEffect(() => {
    const handleResize = () => {
      if (window.innerWidth >= 1280) {
        setItemsToShow(3); // xl screens: show 3
      } else if (window.innerWidth >= 768) {
        setItemsToShow(2); // md screens: show 2
      } else {
        setItemsToShow(1); // mobile: show 1
      }
    };
    
    handleResize();
    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);
  
  // Autoplay functionality
  useEffect(() => {
    if (!isAutoplay) return;
    
    const interval = setInterval(() => {
      nextSlide();
    }, 5000);
    
    return () => clearInterval(interval);
  }, [currentIndex, isAutoplay]);
  
  // Pause autoplay when user interacts
  const pauseAutoplay = () => {
    setIsAutoplay(false);
    // Resume after inactivity
    const timeout = setTimeout(() => setIsAutoplay(true), 10000);
    return () => clearTimeout(timeout);
  };
  
  const nextSlide = useCallback(() => {
    setDirection(1);
    setCurrentIndex((prevIndex) => 
      prevIndex === projects.length - itemsToShow ? 0 : prevIndex + 1
    );
  }, [itemsToShow]);
  
  const prevSlide = useCallback(() => {
    setDirection(-1);
    setCurrentIndex((prevIndex) => 
      prevIndex === 0 ? projects.length - itemsToShow : prevIndex - 1
    );
  }, [itemsToShow]);
  
  // Touch handlers for swipe
  const handleTouchStart = (e: React.TouchEvent) => {
    setTouchStart(e.touches[0].clientX);
  };
  
  const handleTouchMove = (e: React.TouchEvent) => {
    if (!touchStart) return;
    
    const touchEnd = e.touches[0].clientX;
    const diff = touchStart - touchEnd;
    
    // Detect swipe (with threshold of 50px)
    if (Math.abs(diff) > 50) {
      if (diff > 0) {
        nextSlide();
      } else {
        prevSlide();
      }
      setTouchStart(0);
    }
  };
  
  const visibleProjects = projects.slice(currentIndex, currentIndex + itemsToShow);
  
  // If we don't have enough projects to fill the view at the end
  if (visibleProjects.length < itemsToShow) {
    const remaining = itemsToShow - visibleProjects.length;
    visibleProjects.push(...projects.slice(0, remaining));
  }

  return (
    <div id="projects" className="flex flex-col items-center w-full py-16">
      <h2 className="text-4xl sm:text-5xl md:text-4xl lg:text-5xl xl:text-6xl 2xl:text-6xl font-header mb-8">
        Projects
      </h2>
      
      {/* Carousel container */}
      <div 
        className="relative w-full max-w-6xl px-4 py-8"
        onMouseEnter={pauseAutoplay}
        onTouchStart={pauseAutoplay}
      >
        {/* Navigation buttons */}
        <button 
          onClick={() => { prevSlide(); pauseAutoplay(); }}
          className="absolute left-2 top-1/2 -translate-y-1/2 z-10 bg-background/80 backdrop-blur-sm p-2 rounded-full shadow-md border border-border hover:bg-muted transition-colors"
          aria-label="Previous project"
        >
          <ChevronLeft size={20} />
        </button>
        
        <button 
          onClick={() => { nextSlide(); pauseAutoplay(); }}
          className="absolute right-2 top-1/2 -translate-y-1/2 z-10 bg-background/80 backdrop-blur-sm p-2 rounded-full shadow-md border border-border hover:bg-muted transition-colors"
          aria-label="Next project"
        >
          <ChevronRight size={20} />
        </button>
        
        {/* Projects carousel */}
        <div 
          className="overflow-hidden"
          onTouchStart={handleTouchStart}
          onTouchMove={handleTouchMove}
        >
          <AnimatePresence initial={false} custom={direction}>
            <motion.div 
              key={currentIndex}
              custom={direction}
              initial={{ opacity: 0, x: direction > 0 ? 200 : -200 }}
              animate={{ opacity: 1, x: 0 }}
              exit={{ opacity: 0, x: direction > 0 ? -200 : 200 }}
              transition={{ duration: 0.5, ease: "easeInOut" }}
              className={`flex gap-4 md:gap-6`}
            >
              {visibleProjects.map((project) => (
                <div key={project.name} className={`w-full ${
                  itemsToShow === 1 ? 'min-w-full' : 
                  itemsToShow === 2 ? 'min-w-[calc(50%-0.75rem)]' : 
                  'min-w-[calc(33.333%-1rem)]'
                }`}>
                  <ProjectCard
                    name={project.name}
                    description={project.description}
                    technologies={project.technologies}
                    links={project.links}
                  />
                </div>
              ))}
            </motion.div>
          </AnimatePresence>
        </div>
        
        {/* Pagination indicators */}
        <div className="flex justify-center mt-6 space-x-2">
          {Array.from({ length: Math.min(projects.length, projects.length - itemsToShow + 1) }).map((_, idx) => (
            <button
              key={idx}
              className={`w-2.5 h-2.5 rounded-full transition-all duration-300 ${
                idx === currentIndex ? 'bg-primary scale-125' : 'bg-muted'
              }`}
              onClick={() => {
                setCurrentIndex(idx);
                pauseAutoplay();
              }}
              aria-label={`Go to project ${idx + 1}`}
            />
          ))}
        </div>
      </div>
    </div>
  );
};

export default Projects;
